<script>

import {FontAwesomeIcon} from "@fortawesome/vue-fontawesome";
import EndPoints from "@/service/EndPoints";
import{reactive,onMounted,ref} from "vue";
import {handleError} from "@/components/composable/handleError";
import axios from 'axios'
export default {
  name: "LiveResult",
  props:{
    slug:{
      type:String,
      required:true
    }
  },
  components: {FontAwesomeIcon},
  setup(){
    const size=ref(17)
    const listTeams=reactive([
      {name:"برنامه بازی های پرسپولیس"},
      {name:"برنامه بازی های استقلال"},
      {name:"پخش زنده بازی های فوتبال"},
      {name:"جدول لیگ برتر خلیج فارس"},
      {name:"صفحه لیگ قهرمانان اروپا"},
      {name:"صفحه تیم ملی ایران"},
    ])
    const england=reactive([])
    const germany=reactive([])
    const deleteEngland=reactive([])
    const netherlands=reactive([])
    const unionEngland=reactive([])
    const starsQatar=reactive([])
    const showInfo=onMounted(()=>{
      EndPoints.getMatchEngland()
      .then((response)=>{
        if(response.data.success){
          england.push(response)
        }
      })
      .catch((error)=>{
       handleError(error)
      })
    })
    const showGermany=onMounted(async ()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/match/101488'),
        axios.get('https://api.footballi.net/api/v2/match/101481'),
        axios.get('https://api.footballi.net/api/v2/match/101485'),
        axios.get('https://api.footballi.net/api/v2/match/101487'),
      ])
      .then(axios.spread((...response)=>{
        germany.push(response)
      }))
      .catch((error)=>{
      handleError(error)
      })
    })
    const deleteEnglandInfo=onMounted(()=>{
      EndPoints.getDeleteEngland()
      .then((response)=>{
        if(response.data.success){
          deleteEngland.push(response)
        }
      })
      .catch((error)=>{
        handleError(error)
      })
    })
    const showNetherlands=onMounted(()=>{
      axios.all([
        axios.get('https://api.footballi.net/api/v2/match/102268'),
        axios.get('https://api.footballi.net/api/v2/match/102269'),
      ])
      .then(axios.spread((...response)=>{
        netherlands.push(response)
      }))
      .catch((error)=>{
      handleError(error)
      })
    })
    const infoUnion=onMounted(()=>{
      EndPoints.getEnglandunion()
      .then((response)=>{
        if(response.data.success){
          unionEngland.push(response)
        }
      })
      .catch((error)=>{
     handleError(error)
      })
    })
    const infoQatar=onMounted(async()=>{
     await axios.all([
        axios.get('https://api.footballi.net/api/v2/match/102208'),
        axios.get('https://api.footballi.net/api/v2/match/102209'),
        axios.get('https://api.footballi.net/api/v2/match/102210'),
      ])
    })
    return{
      size,
      england,
      showInfo,
      germany,
      showGermany,
      deleteEngland,
      deleteEnglandInfo,
      netherlands,
      showNetherlands,
      unionEngland,
      infoUnion,
      starsQatar,
      infoQatar,
      listTeams,
    }
  }
}
</script>

